import React from 'react';
import './App.css';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Tags from './tags';
import Recommend from './recommend';
import { Paper } from '@material-ui/core';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: "tags"
    };
    this.handleTab = this.handleTab.bind(this);
  }
  handleTab(value) {
    this.setState({
      tab: value
    });
    console.log(this.state.tab);
  }

  render() {
    let tagsTab = <Tags/>;
    let recommendTab = <Recommend/>;
    let showtab;
    if(this.state.tab === "tags"){
      showtab = tagsTab;
    }
    else if (this.state.tab === "recommend"){
      showtab = recommendTab;
    }
    return (
      <div className="App">
        <img src={"/images/LogoFullB.png"} className="App-logo" alt="logo" />
        <div>
        <Paper elevation={0} >
        <ToggleButtonGroup
          value = {this.state.tab}
          exclusive
          aria-label="tabs"
          className="tabsBar"
          size="large"
        >
          <ToggleButton value="tags" onClick={ () => this.handleTab("tags")}> 
              Tags
          </ToggleButton>
          <ToggleButton value="recommend" onClick={() => this.handleTab("recommend")}>
              Recommend
          </ToggleButton>
        </ToggleButtonGroup>
        </Paper>
        <div className="showTab">
          {showtab}
        </div>
        
        </div>
        
      </div>
    );
  }
}



